import { httpService } from "./httpService";

export const transferService = {
  addTransfer,
  getTransfers,
  getTransfersByUserId,
};

async function addTransfer(contactId, amount) {
  return await httpService.post(`transfer/`, { contactId, amount });
}

async function getTransfers() {
  return await httpService.get(`transfer/`);
}

async function getTransfersByUserId(userId) {
  return httpService.get(`transfer/${userId}`);
}
